body {
    font-family: 'Arial', sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
    background: linear-gradient(135deg, #1a1a2e, #16213e, #0f3460, #e94560);
    background-size: 200% 200%;
    animation: gradientAnimation 10s ease infinite;
}

/* Smooth gradient animation */
@keyframes gradientAnimation {
    0% { background-position: 0% 50%; }
    50% { background-position: 100% 50%; }
    100% { background-position: 0% 50%; }
}

.container {
    text-align: center;
}

.clock {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.6); /* Strong shadow for depth */
    background-color: rgba(20, 20, 20, 0.8); /* Darker translucent background */
    backdrop-filter: blur(8px); /* Slight blur effect */
    -webkit-backdrop-filter: blur(8px); /* Add support for Safari */
}

.time {
    display: flex; /* Display items in a row */
    align-items: center; /* Center items vertically */
    font-family: "Montserrat", sans-serif;
    font-size: 6rem; /* Adjust font size as needed */
    color: #ffffff; /* White for time text */
    text-shadow: 4px 4px 8px rgba(0, 0, 0, 0.5); /* Soft shadow */
}

.view {
    font-size: 1rem;
    font-family: "Poppins", sans-serif;
    color: #d1d1d1; /* Light gray for labels */
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    margin-top: 5px;
}

.hours, .minutes, .seconds {
    width: 10rem;
    margin: 0 5px; /* Adjust margin for even spacing */
    color: #ffffff; /* White text for readability */
    background-color: rgba(50, 50, 50, 0.8); /* Dark gray with transparency */
    border-radius: 10px; /* Rounded corners */
    padding: 15px;
    transition: transform 0.2s, background-color 0.3s;
}

/* Subtle hover effect */
.hours:hover, .minutes:hover, .seconds:hover {
    transform: scale(1.05);
    background-color: rgba(100, 100, 100, 0.9); /* Darker gray on hover */
}

.period {
    font-size: 2rem;
    font-family: "Poppins", sans-serif;
    color: #ffffff; /* White for AM/PM text */
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    transition: color 0.3s ease-in-out;
    margin-left: 10px; /* Space between seconds and period */
}

.period:hover {
    color: #e94560; /* Soft accent color for hover */
}

@media (max-width: 786px) {
    .time {
        font-size: 4rem;
    }
    .period {
        font-size: 1.5rem;
    }
}

@media (max-width: 480px) {
    .time {
        font-size: 3rem;
    }
    .hours, .minutes, .seconds {
        width: 8rem; /* Adjust width for smaller screens */
    }
    .period {
        font-size: 1rem;
    }
}
